import styled from 'styled-components';

export const SquareDiv = styled.div`
    border: 1px solid #390040;
    cursor: pointer;
    border-radius: 4px;
    margin: 4px;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`
